import React, { Component } from "react";

export const ProductContext = React.createContext();
ProductContext.displayName = "Product";

export class ProductProvider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      products: []
    };
    this.getProductList = this.getProductList.bind(this);
    this.getSingleProduct = this.getSingleProduct.bind(this);
  }
  componentDidMount() {
    this.getProductList();
  }
  getProductList() {
    fetch("https://shop-api.frameworks.software/api/v1/products/")
      .then(res => {
        if (!res.ok) {
          throw new Error(res.status);
        }
        return res.json();
      })
      .then(data => {
        console.log("ok");
        this.setState({ products: data });
      })
      .catch(err => console.log(err, "pas ok"));
  }

  getSingleProduct(id) {
    return fetch(`https://shop-api.frameworks.software/api/v1/products/${id}`)
      .then(res => res.json())
      .then(data => {
        return data;
      });
  }

  render() {
    const productState = {
      ...this.state,
      getSingleProduct: this.getSingleProduct
    };

    return (
      <ProductContext.Provider value={productState}>
        {this.props.children}
      </ProductContext.Provider>
    );
  }
}

export const ProductConsumer = ProductContext.Consumer;
